import { createFeatureSelector, createSelector} from '@ngrx/store';
import { AdminAppState } from '../admin-app.state';
import { AdminSettingsState } from './admin-settings.state';

export const selectSettingsState = createFeatureSelector<AdminAppState, AdminSettingsState>('adminsettings');
export const selectSettings = createSelector(selectSettingsState, (state: AdminSettingsState) => state);
export const selectTheme = createSelector(selectSettings, (settings) => settings.theme);
export const selectSettingsLanguage = createSelector(selectSettings, (state: AdminSettingsState) => state.language);
