import {Component, OnInit} from '@angular/core';
import {StatisticResponse} from 'src/app/core/models/common/statistic/response/statistic-response.viewmodel';
import {AdminStatisticService} from 'src/app/core/services/admin/statistic/admin-statistic.service';

@Component({
	selector: 'app-index',
	templateUrl: './index.component.html',
	styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {
	statisticItems: Array<StatisticResponse> = [];

	constructor(private adminStatisticService: AdminStatisticService) {}

	ngOnInit(): void {
		this.getStatistics();
	}
	getStatistics() {
		this.adminStatisticService.getStatistics().subscribe((result) => {
			if (result) {
				if (result.isSuccessed) {
					if (result.resultObj) {
						this.statisticItems = result.resultObj;
						console.log(this.statisticItems);
					}
				} else {
				}
			} else {
			}
		});
	}
}
