import {Component, OnInit} from '@angular/core';
import {Validators, FormBuilder, FormGroup} from '@angular/forms';
import {Router} from '@angular/router';
import {AdminLogin} from 'src/app/core/models/admin/authenticate/AdminLogin';
import {AccountService} from 'src/app/core/services/auth/admin/account.service';
import {first, takeUntil} from 'rxjs/operators';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {TranslateService} from '@ngx-translate/core';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';

@Component({
	selector: 'app-login-body',
	templateUrl: './login-body.component.html',
	styleUrls: ['./login-body.component.less'],
	providers: [DestroyService]
	
})
export class LoginBodyComponent implements OnInit {
	submitted = false;
	isLoading = false;
	returnUrl: string;
	adminLoginForm!: FormGroup;
	constructor(
		private accountService: AccountService,
		private formBuilder: FormBuilder,
		private router: Router,
		private destroy$: DestroyService,
		private appTranslateService: TranslateService,
		private notificationService: NotificationService,
		private appErrorHandler: AppErrorHandler,
	) {
		this.returnUrl = '';
	}
	ngOnInit(): void {
		try {
			this.initForm();
		} catch (error) {}
	}
	initForm() {
		try {
			this.adminLoginForm = this.formBuilder.group({
				email: ['', [Validators.required, Validators.minLength(3), Validators.email]],
				password: ['', [Validators.required, Validators.minLength(8), Validators.maxLength(20)]],
				remember: [true]
			});
		} catch (error) {}
	}

	submitForm() {
		try {
			this.submitted = true;
			if (this.adminLoginForm.invalid) {
				return;
			} else {
				this.isLoading = true;
				const val = this.adminLoginForm.value;
				const adminLogin: AdminLogin = {
					email: val.email,
					passWord: val.password,
					remember: val.remember,
					urlReturn: ''
				};
				this.accountService
					.adminlogin(adminLogin)
					.pipe(first(), takeUntil(this.destroy$))
					.subscribe(
						(result) => {
							if (result.message) {
								this.notificationService.showSuccessNotification(
									this.appTranslateService.instant(result.message)
								);

								if (result.isSuccessed) {
									this.router.navigate(['/admin']);
								} else {
									this.submitted = false;
									this.isLoading = false;
								}
							} else {
							}
						},
						(error) => {
							this.submitted = false;
							this.isLoading = false;
							this.appErrorHandler.handleErrorWithNoti(error);
						}
					);
			}
		} catch (error) {
			this.submitted = false;
			this.isLoading = false;
			this.appErrorHandler.handleErrorWithNoti(error);
		}
	}
}
