import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {Store} from '@ngrx/store';
import {NzDrawerPlacement} from 'ng-zorro-antd/drawer';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import { AppErrorHandler } from 'src/app/core/error-handler/app-error-handler.service';
import {AuthService} from 'src/app/core/services/auth/auth.service';
import {AppCookieService} from 'src/app/core/services/comon-services/cookie-service/app-cookie.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {AdminAppState} from 'src/app/core/store/admin/admin-app.state';
import {
	actionSettingsChangeLanguage,
	actionSettingsChangeTheme
} from 'src/app/core/store/admin/setting/admin-setting.actions';
import {selectSettingsLanguage, selectTheme} from 'src/app/core/store/admin/setting/admin-settings.selectors';
import {Language} from 'src/app/core/store/admin/setting/admin-settings.state';
import { AppTheme } from 'src/app/core/store/app/app.state';
@Component({
	selector: 'app-admin-header',
	templateUrl: './admin-header.component.html',
	styleUrls: ['./admin-header.component.css', './admin-header.component.less'],
	providers: [DestroyService]
})
export class AdminHeaderComponent implements OnInit {
	visible = false;
	isLogin?: boolean;
	themes = [
		{value: 'dark', label: 'Dark|Tối'},
		{value: 'default', label: 'light|sáng'}
	];
	languages = [
		{value: 'admin-en', label: 'EN'},
		{value: 'admin-vn', label: 'VN'}
	];
	themeDefault: AppTheme;
	languageDefault: Language;
	//customerInfor: any;
	placement: NzDrawerPlacement = 'left';
	constructor(
		private store: Store<AdminAppState>,
		private destroy$: DestroyService,
		private authService: AuthService,
		private router: Router,
		private appCookieService: AppCookieService,
		private appErrorHandler: AppErrorHandler,
	) {
		this.themeDefault = 'default';
		this.languageDefault = 'admin-vn';
	}

	ngOnInit(): void {
		this.store
			.select(selectTheme)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					this.themeDefault = result;
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
		this.store.select(selectSettingsLanguage).subscribe((result) => {
			this.languageDefault = result != null ? result : 'admin-vn';
		});
		this.checkLogin();
	}
	checkLogin() {
		if (this.authService.isAuthenticated('admin-token')) {
			this.isLogin = true;
		} else {
			this.isLogin = false;
			console.log('customerAuthGuard: false');
		}
	}
	onThemeSelect(theme: AppTheme): void {
		this.store.dispatch(actionSettingsChangeTheme({theme}));
	}
	onLanguageSelect(language: Language) {
		this.store.dispatch(actionSettingsChangeLanguage({language}));
	}
	open(): void {
		this.visible = !this.visible;
	}
	close(): void {
		this.visible = false;
	}
	cancel() {
		return;
	}
	confirm() {
		this.logout();
	}
	logout() {
		console.log('trong admin');
		this.appCookieService.remove('admin-infor');
		this.appCookieService.remove('admin-token');
		this.appCookieService.remove('admin-token');
		this.appCookieService.remove('admin-infor');
		this.router.navigate(['/admin/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
	}
}
