import {Observable} from 'rxjs';
import {ApiResultWithList} from './../../../models/common/ApiResultWithList';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {AdminUrlStatistic} from 'src/app/core/utils/URL/admin-url/admin-url-statistic';
import {StatisticResponse} from 'src/app/core/models/common/statistic/response/statistic-response.viewmodel';

@Injectable({
	providedIn: 'root'
})
export class AdminStatisticService {
	constructor(private adminUrlStatistic: AdminUrlStatistic, private http: HttpClient) {}
	getStatistics(): Observable<ApiResultWithList<StatisticResponse>> {
		return this.http.get<ApiResultWithList<StatisticResponse>>(this.adminUrlStatistic.getStatistic());
	}
}
