import {ActivationEnd, Router} from '@angular/router';
import {Injectable} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {Actions, createEffect, ofType} from '@ngrx/effects';
import {TranslateService} from '@ngx-translate/core';
import {merge, of} from 'rxjs';
import {tap, withLatestFrom, distinctUntilChanged, filter} from 'rxjs/operators';
import {TitleService} from 'src/app/core/services/comon-services/title-service/title.service';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
import {ThemeService} from 'src/app/core/services/comon-services/theme-service/theme.service';
import {actionSettingsChangeLanguage, actionSettingsChangeTheme} from './admin-setting.actions';
import {selectSettingsLanguage, selectSettingsState, selectTheme} from './admin-settings.selectors';
import {AdminAppState} from '../admin-app.state';
export const ADMIN_SETTINGS_KEY = 'adminsettings';

const INIT = of('anms-init-effect-trigger');

@Injectable()
export class AdminSettingsEffects {
	setTitle = createEffect(
		() =>
			merge(
				this.actions$.pipe(ofType(actionSettingsChangeLanguage)),
				this.router.events.pipe(filter((event) => event instanceof ActivationEnd))
			).pipe(
				tap(() => {
					this.titleService.setTitle(this.router.routerState.snapshot.root, this.translateService);
				})
			),
		{dispatch: false}
	);

	updateTheme = createEffect(
		() =>
			merge(INIT, this.actions$.pipe(ofType(actionSettingsChangeTheme))).pipe(
				withLatestFrom(this.store.pipe(select(selectTheme))),
				tap(([, effectiveTheme]) => this.themeService.loadTheme(false, effectiveTheme))
			),
		{dispatch: false}
	);
	setTranslateServiceLanguage = createEffect(
		() =>
			this.store.pipe(
				select(selectSettingsLanguage),
				distinctUntilChanged(),
				tap((language) => this.translateService.use(language))
			),
		{dispatch: false}
	);
	persistSettings = createEffect(
		() =>
			this.actions$.pipe(
				ofType(actionSettingsChangeLanguage, actionSettingsChangeTheme),
				withLatestFrom(this.store.pipe(select(selectSettingsState))),
				tap(([, settings]) => {
					this.localStorageService.setItem(ADMIN_SETTINGS_KEY, settings);
				})
			),
		{dispatch: false}
	);
	constructor(
		private actions$: Actions,
		private store: Store<AdminAppState>,
		private router: Router,
		private localStorageService: LocalStorageService,
		private titleService: TitleService,
		private translateService: TranslateService,

		private themeService: ThemeService
	) {}
}
