import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AdminGuardGuard} from 'src/app/core/guards/admin/admin-guard.guard';

import {AdminComponent} from './admin.component';
import {FinanceComponent} from './components/admin-body/finance/finance.component';
import {HomeComponent} from './components/admin-body/home/home.component';
import {IndexComponent} from './components/admin-body/index/index.component';
import {MarketingComponent} from './components/admin-body/marketing/marketing.component';
//import {OrderComponent} from './components/admin-body/order/order.component';

const routes: Routes = [
	{
		path: '',
		component: AdminComponent,
		canActivateChild: [AdminGuardGuard],
		children: [
			{path: '', component: IndexComponent},
			{path: 'finance', component: FinanceComponent},
			{path: 'home', component: HomeComponent},
			{path: 'marketing', component: MarketingComponent},
			//{path: 'order', component: OrderComponent},
			{
				path: 'product',
				loadChildren: () =>
					import('./components/admin-body/product/product.module').then((m) => m.ProductModule)
			},
			{
				path: 'order',
				loadChildren: () =>
					import('./components/admin-body/admin-order/admin-order.module').then((m) => m.AdminOrderModule)
			},
			{
				path: 'author',
				loadChildren: () =>
					import('./components/admin-body/Author/Author.module').then((m) => m.AuthorModule)
			},
			{
				path: 'publisher',
				loadChildren: () =>
					import('./components/admin-body/publisher/publisher.module').then((m) => m.PublisherModule)
			}
		]
	},
	{
		path: 'login',
		loadChildren: () => import('./modules/login/login.module').then((m) => m.LoginModule)
	},
	{
		path: 'register',
		loadChildren: () => import('./modules/register/register.module').then((m) => m.RegisterModule)
	},
	{
		path: 'notfound',
		loadChildren: () =>
			import('../seller/module/seller-notfound/seller-notfound.module').then((m) => m.SellerNotfoundModule)
	},
	{path: '**', redirectTo: 'notfound'}
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class AdminRoutingModule {}
