import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
@Component({
	selector: 'app-admin',
	templateUrl: './admin.component.html',
	styleUrls: ['./admin.component.less'],
	providers: [DestroyService],
	encapsulation: ViewEncapsulation.None
})
export class AdminComponent implements OnInit {
	isSubmited: boolean;
	constructor(
		private adminSpinnerService: SpinnerService,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler
	) {
		this.isSubmited = false;
	}
	ngOnInit(): void {
		this.controlSpinner();
	}
	controlSpinner() {
		this.adminSpinnerService.subjectSubmitLoadingHaveData$.pipe(takeUntil(this.destroy$)).subscribe(
			(result) => {
				this.isSubmited = result.isSubmitted;
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
}
